.App {
  display: flex;
  flex-direction: row;
  height: 80em;
}

.modal-backdrop {
  background-color: rgba(0, 0, 0, 0.288) !important;
}

.graafi {
  margin-top: 21em;
  margin-left: 1em;
}

.alert {
  z-index: 10001;
  white-space: pre-line;
}

.Flex {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height:65em;
  width: 70em;
  align-items: center;
  margin: 2em;
}

.Navbar {
  width: 200%;
  position: fixed;
  display: flex;
  flex-direction: row;
  place-items: 1em;
  align-items: center;
  border-radius: 0 !important;
  -moz-border-radius: 0 !important;
  z-index: 10000;
}

.nappi {
  margin-left: 1em;
}

.Laatikko {
  padding: 2em;
  background-color: #67B9CD;/*#214E75;/*#096484;*/
  width: 95%;
  border-radius: 4px;
}

.laatikkoDiv {
  margin: 1em;
  width: 30em;
  border-radius: 4px;
}

.makeStyles-root-1 {
  text-align: center;
  margin: 1em;
}

.laatikkoP {
  color: aliceblue;
}

p b {
  color: #3278B6;/*#5C95C5;/*#337AB7*/
}

.Haku {
  width: 11em;
  font-weight: 100;
}

.hakuTiedot {
  width: 30em;
  margin-top: 18em;
  flex-direction: row;
}

.leaflet-container {
  margin-top: 5em;
  width: 40em;
  height: 50em;
  }

@media screen and (max-width: 1620px) {
  .App {
    height: 80em;
    flex-direction: column
  }
  .graafi {
    margin-top: 0em;
    margin-left: 1em;
    padding: 1em;
  }
}

@media screen and (max-width: 990px) {
  .App {
    height: 80em;
    width: 40em;
  }

  .leaflet-container {
    width: 40em;
    height: 50em;
    margin-bottom: 1em;
  }

  .laatikkoDiv {
    width: 20em;
    border-radius: 4px;
  }

  .Haku {
    margin-top: 1em;
  }

  .hakuTiedot {
    width: 30em;
    margin-top: 18em;
    margin-left: 1em;
    flex-direction: row;
  }
  
  .Flex {
    align-items: unset;
  }

  .makeStyles-root-1 {
    margin-left: 5.5em;
  }
}

@media screen and (max-width: 840px) {
  .App {
    height: 120em;
    flex-direction: column;
  } 

  .Flex {
    height: 105em;
    align-items: baseline;
    margin-bottom: 0em;
  }

  .hakuTiedot {
    width: 30em;
    margin-top: 0em;
    margin-left: 1em;
    flex-direction: row;
  }

  .laatikkoDiv {
    width: 28em;
  }
}

.footer{
  max-height: 40px;
  height: 5em;
  margin-top: 1em;
  padding: 1em;
  background-color: #67B9CD;/*#8DA4B9;/*rgb(181, 201, 216);*/
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9000;
}

body {
  margin: 0px;
}

.info {
  padding: 6px 8px;
  font: 14px/16px Arial, Helvetica, sans-serif;
  background: white;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}

.info h4 {
  margin: 0 0 5px;
  color: #777;
}

.legend {
  text-align: left;
  line-height: 18px;
  color: #555;
}

.legend i {
  width: 2em;
  height: 1em;
  float: left;
  margin-right: 8px;
  opacity: 0.7;
}

